@media screen and (max-width: 1102px) {
  .caution {
    box-shadow: none;
    margin: 0 1.5em;
    p {
     font-size: 3.3rem;
     box-shadow: 0 0 1.1px #000; } } }

@media screen and (max-width: 992px) {
  .storage {
    .caution {
      p {
       font-size: 2.5rem; } } } }

@media screen and (max-width: 756px) {
  .storage {
    .btn-box {
      margin: 50px 0 0 0; }
    p {
      font-size: 1.6rem;
      letter-spacing: 0.5px;
      margin: 0; }
    .btn {
        margin-bottom: 7rem; }

    .caution {
      p {
        font-size: 2rem;
        letter-spacing: 2.5px;
        margin: 0 10px;
        top: 25px; } } } }



@media screen and (max-width: 576px) {
  .storage {
    p {
      font-size: 1.4rem;
      padding: 0 4em; }


    .btn-box {
      margin: 4em 0 0 0;
      .btn {
        width: 25rem;
        font-size: 1.5rem; } }


    .caution {
      margin: 0;
      p {
        font-size: 2rem;
        letter-spacing: -1.5px;
        padding: 0;
        transform: scaleY(2);
        letter-spacing: 2.5px;
        top: 20px; } }
    .btn--instr {
      margin-bottom: 2rem; } } }

@media screen and (max-width: 472px) {
  .storage {
    .caution {
      p {
        font-size: 2rem;
        letter-spacing: -2.5px;
        transform: scaleY(2.5); } } } }
