header {
  position: fixed;
  z-index: 999;
  width: 100%;
  top: 0;
  overflow: hidden;
  background-color: $c-purple;
  color: $c-white;
  opacity: 0;
  & .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2em 0;
    & .logo {
      margin-left: 1em;
      & a {
        color: $c-white;
        font-size: $size-35;
        font-weight: 500;
        transition: opacity 0.2s linear;
        &:hover {
          opacity: 0.7; } } }


    & nav {
      width: 65%;
      margin-left: 50px;
      display: flex;
      justify-content: space-around;
      & a {
        color: $c-white;
        font-size: $size-24;
        font-weight: 300;
        position: relative;
        &:hover {
          opacity: 0.7;
          transition: 0.2s linear; } } } } }



.fixed {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100; }

.menu-slim {
  display: none; }



.mobile-menu {
  .nav-mobile {
    display: none; } }
